export default {
	props: {
		subtitle: {
			type: String
		},
		title: {
			type: String
		},
		icon:{
			type:String
		},
		more:{
			type:String
		},
		url_right:{
			type:String
		},
		url_left:{
			type:String
		}
	},
	data() {
		return {
			// title: '',
			// subtitle: ''
		}
	},
	methods:{
		back(url_left){
			if(url_left){
				if(url_left == ''){
					uni.navigateBack({
						delta:1
					})
				}else if(url_left=='/pages/index/me'||url_left=='/pages/index/index'||url_left=='/pages/index/life'||url_left=='/pages/index/share'){
					// 判断是不是跳转到tabbar页面
						uni.switchTab({
							url:url_left
						})
					}else{
						//跳转到有url_left的页面
					uni.navigateTo({
						url:url_left
					})
				}
			}else{
				uni.navigateBack({
					delta:1
				})
			}
			
		},
		jump(url){
			if(url=='/pages/index/me'||url=='/pages/index/index'||url=='/pages/index/life'||url=='/pages/index/share'){
				uni.switchTab({
					url:url
				})
			}else{
				uni.navigateTo({
					url:url
				})
			}
			
		}
	}
}
